  border: none;
  background-color: #70b4ff;
  padding: 5px 10px;
  border-radius: 5px;
  color: white;
}

/* *************************** */

/* On mouse-over, add a grey background color */
.parent_checkbox:hover input ~ .checkmark {
  background-color: #70b4ff;
}

/* When the checkbox is checked, add a blue background */
.parent_checkbox input:checked ~ .checkmark {
  background-color: #70b4ff;
  border: none;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.parent_checkbox input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.parent_checkbox .checkmark:after {
  left: 9px;
  top: 4px;
  width: 7px;
  height: 14px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

