/* Custom checkboxes */

.span_pseudo,
.chiller_cb span:before,
.chiller_cb span:after {
  content: "";
  display: inline-block;
  background: #fff;
  width: 0;
  height: 0.2rem;
  position: absolute;
  transform-origin: 0% 0%;
}

.chiller_cb {
  position: relative;
  height: 2rem;
  display: flex;
  align-items: center;
}

.chiller_cb input {
  display: none;
}

.chiller_cb input:checked ~ span {
  background: #70b4ff;
  border-color: #70b4ff;
}

.chiller_cb input:checked ~ span:before {
  width: 14px;
  height: 0.15rem;
  transition: width 0.1s;
  transition-delay: 0.3s;
}

.chiller_cb input:checked ~ span:after {
  width: 0.4rem;
  height: 0.15rem;
  transition: width 0.1s;
  transition-delay: 0.2s;
}

.chiller_cb input:disabled ~ span {
  background: #ececec;
  border-color: #dcdcdc;
}

.chiller_cb input:disabled ~ label {
  color: #dcdcdc;
}

.chiller_cb input:disabled ~ label:hover {
  cursor: default;
}

.chiller_cb label {
  padding-left: 2rem;
  position: relative;
  z-index: 2;
  cursor: pointer;
  margin-bottom: 0;
}

.chiller_cb span {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border: 2px solid #ccc;
  position: absolute;
  left: 0;
  transition: all 0.2s;
  z-index: 1;
  box-sizing: content-box;
  border-radius: 4px;
}

.chiller_cb span:before {
  transform: rotate(310deg);
  top: 13px;
  left: 4px;
}

.chiller_cb span:after {
  transform: rotate(35deg);
  bottom: 5px;
  left: 2px;
}

